.navigation{position:fixed;right:30px;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);z-index:200}@media (max-width:1278px){.navigation{right:15px}}.navigation__item{width:20px;height:20px;border-radius:50%;background:#fff;margin:15px 0;cursor:pointer;opacity:.5}@media (max-width:1278px){.navigation__item{width:15px;height:15px}}.navigation__item--active,.navigation__item:hover{opacity:1}