ant}.main-screen__icon--3{background:url(images/svg-sprite/badges-3.svg);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important}.main-screen__icon--4{background:url(images/svg-sprite/badges-4.svg);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important}.main-screen__icon--5{background:url(images/svg-sprite/badges-5.svg);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important}.main-screen__logo-mobile{display:none;position:absolute;left:2vw;top:41vh;width:50vw}@media (max-width:1278px){.main-screen__logo-mobile{display:block}}.main-screen__logo-mobile-img{width:100%;height:30vw;background:url(images/svg-sprite/logo-label.svg);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important;-webkit-filter:drop-shadow(8px 15px 14px rgba(0,0,0,.7));filter:drop-shadow(8px 15px 14px rgba(0,0,0,.7));margin-left:auto;margin-right:auto}.main-screen__logo-mobile-text{font-family:Philosopher,sans-serif;font-family:Caveat,cursive;font-family:Neucha,cursive;font-family:Bad Script,cursive;font-family:nefelibata,cursive;text-align:center;font-style:italic;font-weight:400;line-height:8vw;width:100%;font-size:6vw}.main-screen__logo-mobile-sign{width:100%;height:15vw;background:url(images/sign.png);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important;margin-top:1vw;margin-left:auto}