.last-screen{position:absolute;width:100%;height:100%;top:0;left:0}.last-screen__title{width:100vw;height:14vw;background:url(images/il_gusto.png);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important;position:absolute;top:calc(50% - 18vw);left:0}@media (max-width:1278px){.last-screen__title{top:19%;height:15vh}}.last-screen__labels{position:absolute;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;top:calc(50% - 5vw);left:50%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%)}@media (max-width:1278px){.last-screen__labels{top:60%;-webkit-transform:translateY(-100%) translateX(-50%);-ms-transform:translateY(-100%) translateX(-50%);transform:translateY(-100%) translateX(-50%)}}.last-screen__social{position:absolute;top:calc(50% + 16vw);width:40vw;left:50%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%)}@media (max-width:1278px){.last-screen__social{top:65%;-webkit-transform:translateX(-50%);-ms-transform:translateX(-50%);transform:translateX(-50%)}}.last-screen__social-title{font-family:-apple-system,blinkmacsystemfont,Segoe UI,roboto,Helvetica Neue,arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;font-size:1.4vw;text-align:left;margin-bottom:2vw}@media (max-width:1278px){.last-screen__social-title{font-size:8vw;margin-bottom:5vw}}.last-screen__social-items{display:grid;grid-template-columns:repeat(2,14vw);grid-gap:1vw;position:relative;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}@media (max-width:1278px){.last-screen__social-items{grid-template-columns:1fr;grid-gap:3vw}}.last-screen__social-items:after{content:"";position:absolute;display:block;left:50%;top:0;height:100%;width:1px;background:#fff}@media (max-width:1278px){.last-screen__social-items:after{display:none}}.last-screen__social-item{color:#fff;font-size:1vw;text-align:left;padding-left:1.8vw;position:relative}@media (max-width:1278px){.last-screen__social-item{font-size:4vw;padding-left:7vw}}.last-screen__social-item:hover{text-decoration:underline}.last-screen__social-item:after{content:"";position:absolute;display:block;width:1.5vw;height:1.5vw;background:#fff;top:50%;left:0;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}@media (max-width:1278px){.last-screen__social-item:after{width:5vw;height:5vw}}.last-screen__social-item--site:after{-webkit-mask:url(images/svg-sprite/icon-www.svg);mask:url(images/svg-sprite/icon-www.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.last-screen__social-item--inst:after{-webkit-mask:url(images/svg-sprite/icon-insta.svg);mask:url(images/svg-sprite/icon-insta.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.last-screen__social-item--mail:after{-webkit-mask:url(images/svg-sprite/icon-email.svg);mask:url(images/svg-sprite/icon-email.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.last-screen__social-item--vk:after{-webkit-mask:url(images/svg-sprite/icon-vk.svg);mask:url(images/svg-sprite/icon-vk.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}