.wrapper{position:fixed;width:100%;height:100%;top:0;left:0;background:url(images/background/bg-pattern--blue.jpg);background-size:cover!important;background-repeat:no-repeat!important;background-position:50%!important}.wrapper__cook{content:"";position:absolute;display:block;background:url(images/cook.png);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important;background-position:bottom!important;bottom:0;left:calc(50% + 13vw);width:21.3vw;height:100%;opacity:.001;-webkit-transition:all .5s ease-in;-o-transition:all .5s ease-in;transition:all .5s ease-in;z-index:1}.wrapper--0 .wrapper__cook{opacity:1}@media (max-width:1278px){.wrapper__cook{right:-5vh;left:auto;width:100vw;height:45vh;background-position:100% 100%!important}}.wrapper__inner{position:absolute;width:100%;height:100%;top:0;left:0;-webkit-transition:all .5s ease-in;-o-transition:all .5s ease-in;transition:all .5s ease-in}.wrapper__inner--blue{background:url(images/background/bg-pattern--blue.jpg);background-size:cover!important;background-repeat:no-repeat!important;background-position:50%!important;opacity:.001}.wrapper--1 .wrapper__inner--blue,.wrapper--default .wrapper__inner--blue{opacity:1}.wrapper__inner--green{background:url(images/background/bg-pattern--green.jpg);background-size:cover!important;background-repeat:no-repeat!important;background-position:50%!important;opacity:.001}.wrapper--2 .wrapper__inner--green{opacity:1}.wrapper__inner--turquoise{background:url(images/background/bg-pattern--turquoise.jpg);background-size:cover!important;background-repeat:no-repeat!important;background-position:50%!important;opacity:.001}.wrapper--3 .wrapper__inner--turquoise{opacity:1}.wrapper:after{content:"";position:absolute;display:block;width:100%;height:100%;top:0;left:0;background:url(images/background/bg-draft.png);background-size:contain!important;background-repeat:no-repeat!important;background-position:50%!important}