.scroll-screen{position:fixed;left:0;top:0;width:100%;z-index:2;-webkit-transition:all .5s ease-in;-o-transition:.5s all ease-in;transition:all .5s ease-in}@media (max-width:1278px){.scroll-screen{position:relative;left:auto;top:auto}}.scroll-screen__item{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:50px;text-align:center;width:100%;height:100vh;color:#fff;position:relative}